import React, { useEffect } from "react";
import styled from "styled-components";
import NavBar from "../Navbar";
import Button from "@material-ui/core/Button";
import Footers from "./footer";
import BusinessIcon from "@material-ui/icons/Business";
import CheckIcon from "@material-ui/icons/Check";
import StarIcon from "@material-ui/icons/Star";
import FlareIcon from "@material-ui/icons/Flare";
import axios from 'axios';
import { Link ,useParams,useHistory} from "react-router-dom";
import { useFlutterwave, closePaymentModal } from "flutterwave-react-v3";
import { useSelector,useDispatch } from "react-redux";
import {SYNCUSERDATA } from "../../redux/action";

import Switch from "react-switch";


function numberWithCommas(x) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

const Container = styled.div`
  min-height: 300px;
  text-align: center;
  //   padding: 10px;
  padding-top: 130px;
  width: 100%;
`;
const Buttons2 = styled(Button)`
  background-color: #00a82d;
  color: #ffffff;
`;

const BigText = styled.p`
  font-size: 60px;
  color: black;
  // line-height: 2;
  font-weight: 600;
  @media (max-width: 1100px) {
    font-size: 30px;
  }
`;

const PlansSection = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  flex-wrap: wrap;
  padding: 10px;
  box-sizing: border-box;
`;

const Flexcards = styled.div`
  width: 400px;
  // border: 1px solid #00a82d;
  min-height: 400px;
  border-radius: 8px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;

  align-items: center;
`;
const Flexcards2 = styled.div`
  position: relative;

  width: 300px;
  border: 1px solid #00a82d;
  min-height: 400px;
  max-height:420px;
  border-radius: 8px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;

  align-items: center;
`;
const Listing = styled.ul`
  display: flex;
  margin: 0;
  list-style-type: none;
  padding: 0;

  flex-direction: column;
  align-items: flex-start;
  width: 98%;
  li {
    margin-top: 2px;
    margin-bottom: 2px;
    width:100%;
    text-align:left;
   
  }

  .pay-options{
    @media(max-width:680px){
      width:100% !important;
    }
  }

  .cards{
    display:flex;
    margin-top:30%;
    flex-direction:row;
    align-items:center;
    flex-wrap:wrap;
  }
  .switch{
    display:flex;
    flex-direction:row;
    align-items:center;
 span{
   margin-left:10px;
   margin-right:10px;

 }
  
  }

`;
const GreenHeader = styled.div`
  width: 100%;
  background-color: #00a82d;
  height: 20px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  position: absolute;
  top: 0;
  margin-bottom: auto;
`;

const Buttons = styled(Button)`

  && {
    color: #fffff;
    background-color: white;
    &:focus {
      background-color: white;
    }
   
  }
`;
const ButtonsStyle = {
  color: "#00a82d",
  borderColor: "#00a82d",
  width: "200px",
  alignSelf: "center",
};
const Mybuttons = {
  backgroundColor: "#00a82d",
  color: "#ffffff",
  width: "300px",
};

const MidText = styled.p``;
const AlmostThere = () => {
  const dispatch=useDispatch()
  const CurrentUser = useSelector((state) => state.user.currentUser);
  const userdata = CurrentUser && CurrentUser.userdata;
  const [checked,setChecked]= React.useState(false)
  const [paymethod,setPaymenthod]= React.useState(1)
  const currentUser = useSelector((state) => state.user.currentUser);


  const token = currentUser && currentUser.token;
  const {plans} = useParams() ||""

const history=useHistory()
const {price} = history.location.state ||""
  useEffect(()=>{
plans&&plans!=="Business" &&plans!=="Premium" &&history.push("/")
  },[])
  const config = {
    // public_key: process.env.REACT_APP_FLUTTERWAVE_PUBLIC_KEY,
    // public_key:process.env.REACT_APP_FLUTTERWAVE_PUBLIC_KEY,
    public_key:'FLWPUBK_TEST-e96ca53411ac8244d05c06677fca3509-X',
    tx_ref: new Date().getTime() + "-" + userdata._id,
    amount: parseFloat(price),
    currency:  "NGN",
    payment_options: "card",
    customer: {
      email: userdata.Email,
      // phonenumber: PostRommsAddReducer.mobile_numbe,
      name: userdata.Email,
    },
    customizations: {
      title: "RoomNets Ad Payment",
      description: "payment Add Post Premimum",
      logo:
        "https://st2.depositphotos.com/4403291/7418/v/450/depositphotos_74189661-stock-illustration-online-shop-log.jpg",
    },
  };

  const PayByPaypal=async ()=>{

  }
  const handleValidatePayment= React.useCallback(async(response,selected)=>{
    const params={
      payment_response:response,
      plan:selected,
      plan_date:checked?'month':"year"
    }

    await axios.post(`${process.env.REACT_APP_API_URL}/users/handleUpgradePlans`,params,{ headers: { authorization: token } }).then(res=>{
    if(res.data.status){
    
      dispatch(SYNCUSERDATA(res.data.userdata))
          history.replace("/success-pay");
    }
    }).catch(error=>{
      console.log(error)
    })
    
  
  


      },[])
  const handleChange=()=>{
    setChecked(!checked)
  }
  const handleFlutterPayment = useFlutterwave(config);
  const PayByflutterWave = async () => {
    await handleFlutterPayment({
      callback: (response) => {
        
        closePaymentModal(); // this will close the modal programmatically
        if (response.status === "successful") {
         
          // handleSuccess();
          handleValidatePayment(
            response,
            plans,
     
          );
        }
      },
      onClose: () => {},
    });
  };
  return (
    <>
      <NavBar />

      <Container>
        <PlansSection>
          <Flexcards>
            <Listing >
              <li>
                <BigText style={{ fontSize: "26px" }}>
                  You are just few seconds away
                </BigText>
              </li>
              <li>Payment method : </li>
              <li>
                <Buttons className='pay-options'  onClick={()=>{setPaymenthod(1)}}
                  style={{
                    ...Mybuttons,
                    backgroundColor: "#ffffff",
                    border: "1px solid #00a82d",
                    color: "#00a82d",
                    marginBottom: "10px",
                    marginTop: "10px",
                  }}
                >
                  Pay With Card  {paymethod===1&& <CheckIcon />}
                </Buttons>
              
              </li>
              <li>
                <Buttons className='pay-options' onClick={()=>{setPaymenthod(2)}} style={{ ...Mybuttons }}>Pay with Paypal   {paymethod===2&& <CheckIcon />}</Buttons>
              </li>
              <li className='cards'>
                <img width='100px' src='/images/quickteller.jpg' alt=''/>
                <img width='100px' src='/images/visa.png' alt=''/>
                <img width='150px' src='/images/mastercard.png' alt=''/>
              </li>
            </Listing>
          </Flexcards>

          <Flexcards2>
            <GreenHeader />
            <Listing style={{height:"100%",}}>
              <li>
                <BigText style={{ fontSize: "26px" }}>{plans}</BigText>
              </li>
              <li>Organize everything</li>
              <li>
                <BigText style={{ fontSize: "26px", lineHeight: "0%" }}>
                  NGN {price}.0
                </BigText>
              </li>
             
              <li>
                {" "}
                <BigText
                  style={{
                    fontSize: "18px",
                    color: "grey",
                    textDecoration: "line-through",
                    lineHeight: "0%",
                  }}
                >
                  NGN {price?price+price/2:null}.0
                </BigText>
              </li>
              <li className='switch'><span>Monthly</span> <Switch  offColor={'#080'} onColor={'#080'} uncheckedIcon={false}  checkedIcon={false} onChange={handleChange} checked={checked} /> <span>Yearly</span></li>
              <li>
                <Buttons onClick={()=>{
              paymethod===1&&PayByflutterWave()
              paymethod===2&&PayByPaypal()
              
                }} style={{ ...Mybuttons,width:"100%",marginTop:"30%" }}>Pay Now</Buttons>
              </li>
            </Listing>
          </Flexcards2>
        </PlansSection>
      </Container>
      <Footers />
    </>
  );
};

export default AlmostThere;
