import React from "react";
import styled from "styled-components";
// import { Colors } from "../themes";
import Button from "@material-ui/core/Button";
import {Link} from 'react-router-dom'

const Footer = styled.footer`
  background-color: white;
  min-height: 200px;
  // width: 80vw;
  padding: 10px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
`;

const BigText = styled.p`
  font-size: 16px;
  color: #696969;
  font-weight: 500;
font-weight:900;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  &:nth-child(n) {
    margin-right: 20px;
  }
`;
const MidTextfoot = styled.p`
  font-size: 14px;
  color: grey;
  font-weight: 500;
  &:nth-child(n) {
    margin-right: 20px;
  }
`;
const SmallTextfoot = styled.p`
  font-size: 12px;
  color: grey;
  font-weight: 200;
`;
const FooterSection = styled.div`
  display: flex;
  flex-direction: row;
  width: 70%;
  justify-content: space-between;

  flex-wrap: wrap;
  @media(max-width:630px){
    width: 100%;
    justify-content: cemter;
  }
`;

const Div = styled.div`
  display: flex;
  min-width: 100px;
  flex-direction: row;
  align-items: center;
`;

const Buttons = styled(Button)`
  && {
    color: #00a82d;
    border-color: #00a82d;
    &:focus {
      background-color: blue;
    }
  }
`;
const Logo = styled.img`
width:300px;
//  object-fit:contain;
 max-height:80px;
  align-self: flex-start;
  @media (max-width: 680px) {
    margin-right:40px;
 }
`;

const Listing = styled.ul`
  &:nth-child(n) {
    margin-right: 20px;
  }
  list-style: none;

  li {
    margin-bottom: 20px;
  }
`;

const Divider = styled.div`
  width: 90VW;
  height: 2px;
  background-color: #00a82d;
margin-top:80px;
margin-bottom: 70px;
`;
const DividerBottom = styled.div`
  width: 90VW;
  height: 1px;
  background-color: #00a82d;
  margin-top:30px;
  Margin:-bottom: 20px
`;
const Bottomdiv = styled.div`
  display: flex;
  min-width: 90%;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 60px;
`;
const Footers = () => {
  return (
    <Footer>
      <Logo src="/images/script2.png" />
      <Divider />
      <FooterSection>
        <Listing>
          <li>
            <BigText>PRODUCT</BigText>
          </li>
          <li>
            <MidTextfoot>  <Link to="/WHYNOTES">Why Notes</Link> </MidTextfoot>
          </li>
        
        </Listing>
        <Listing>
          <li>
            <BigText>FEATURES</BigText>
          </li>
          <li>
            <MidTextfoot>  <Link to="/plans">Notes Premium</Link></MidTextfoot>
          </li>
          <li>
            <MidTextfoot>  <Link to="/plans">Notes Business </Link></MidTextfoot>
          </li>
         
        </Listing>
        <Listing>
          <li>
            <BigText>COMUNITY</BigText>
          </li>
          <li>
           
          </li>
       
        </Listing>

        <Listing>
          <li>
            <BigText>COMPANY</BigText>
          </li>
          <li>
            <MidTextfoot><Link to='/Aboutus'>About us</Link> </MidTextfoot>
          </li>
          <li>
            <MidTextfoot><Link to='/Privacy'>Privacy policy</Link></MidTextfoot>
          </li>
         
        </Listing>
      </FooterSection>
      <DividerBottom />
      <Bottomdiv>
        <SmallTextfoot>2021 All Rights Reserved</SmallTextfoot>
        <SmallTextfoot><Link to='/Privacy'>Privacy policy</Link></SmallTextfoot>
      </Bottomdiv>
    </Footer>
  );
};

export default Footers;
