import React from "react";
import styled from "styled-components";
import NavBar from "../Navbar";
import Button from "@material-ui/core/Button";
import Footers from "./footer";
import BusinessIcon from "@material-ui/icons/Business";
import CheckIcon from "@material-ui/icons/Check";
import StarIcon from "@material-ui/icons/Star";
import FlareIcon from "@material-ui/icons/Flare";
import { Link } from "react-router-dom";
import axios from "axios";

const Container = styled.div`
  min-height: 300px;
  text-align: center;
  //   padding: 10px;
  padding-top: 130px;
  width: 100%;
`;
const Buttons = styled(Button)`
  width: 200px;
  height: 40px;

  && {
    margin:3px;

    color: #00a82d;
    border-color: #00a82d;
    &:focus {
      background-color: blue;
    }
`;
const BigText = styled.p`
  font-size: 60px;
  color: black;
  font-weight: 600;
  @media (max-width: 1100px) {
    font-size: 30px;
  }
`;

const PlansSection = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px;
  box-sizing: border-box;
`;

const Flexcards = styled.div`
  width: 320px;
  border: 1px solid #00a82d;
  min-height: 400px;
  border-radius: 8px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
    @media(max-width:680px){
    margin-left: 2px;
    margin-right: 3px;
    width:90%;
  }
`;
const Listing = styled.ul`
  display: flex;
  margin: 0;
  list-style-type: none;
  padding: 0;

  flex-direction: column;
  align-items: flex-start;
  width: 98%;
  li {
    display: flex;
    font-size: 16px;
    color: black;
    padding: 10px;
    &:nth-child(1) {
      font-size: 20px;
      color: grey;
    }
    &:nth-child(2) {
      font-size: 30px;
      color: black;
      font-weight: 600;
      @media (max-width: 1100px) {
        font-size: 30px;
      }
    }
    &:nth-child(3) {
      font-size: 20px;
      color: grey;
      font-weight: 500;
      @media (max-width: 1100px) {
        font-size: 20px;
      }
    }
  }
`;
const MidText = styled.p``;
const Plans = () => {
  const [isLoading,setIsLoading] =React.useState('')
const[homedata,setHomedata]= React.useState({users:0,notes:0})

const [pageData,setPageData]=React.useState({premium:0,business:0})
  const getPlans= (values) => {

    setIsLoading(true);
  // console.log(process.env.REACT_APP_API_URL)
    axios
      .get(`${process.env.REACT_APP_API_URL}/plans/getplans`)
      .then((res) => {
        setIsLoading(false);
        console.log(res.data);
        setPageData({...pageData,...res.data.userData})
      
      })
      .catch((err) => {
        setIsLoading(false);
        console.log(err)
      
      });
  };
  React.useEffect(()=>{
    getPlans()
  },[])


  return (
    <>
      <NavBar />
      <Container>
        <BigText>Which notes plan is the best for you</BigText>
        
        <PlansSection>
          <Flexcards>
            <Listing>
              <li>
                <BusinessIcon size={18} style={{ marginRight: "5px" }} /> Basic
              </li>
              <li>Free</li>
              <li>Save ideas and find them quickly </li>
              <li>
                <CheckIcon size={15} />Get organized with and 3 widgets
              </li>
              <li>
                <CheckIcon size={15} />Stay on top of it all 
              </li>
              <li>
                <CheckIcon size={15} /> search things fast with search and tags
              </li>
              <li>
                <CheckIcon size={15} />Stay on top of it with your notes
                </li>
              <li>
                <CheckIcon size={15} /> Save up to 5 notes for free plan
              </li>
              <li>
                <CheckIcon size={15} /> 10 MB max. note size
              </li>
           
            </Listing>
            <Link to="/login/signup">
              <Buttons
                variant="outlined"
                color="primary "
                style={{ backgroundColor: "white", color: "#00a82d" }}
              >
                Get started
              </Buttons>
            </Link>
          </Flexcards>
          <Flexcards>
            <Listing>
              <li>
                <StarIcon
                  size={18}
                  style={{ color: "#00a82d", marginRight: "5px" }}
                />{" "}
                Premium
              </li>
              <li>=N={pageData.premium}</li>
              <li>
                <CheckIcon size={15} /> connect unlimited devices
              </li>
              <li>
                <CheckIcon size={15} /> Customize your notes how you like it
              </li>
              <li>
              <CheckIcon size={15} /> Save up to 20 notes 
              </li>
              <li>
                <CheckIcon size={15} />Manage projects and task in one place
              </li>
              <li>
                <CheckIcon size={15} />Get unlimited access on mobile and desktop
              </li>
              <li>
                <CheckIcon size={15} />Add due dates notifications to your notes
              </li>
              <li>
                {" "}
                <CheckIcon size={15} />Save great notes 
              </li>
              <li>
                {" "}
                <CheckIcon size={15} /> multi text format{" "}
              </li>
            </Listing>
            <Link to="/login/signup">
              <Buttons
                variant="outlined"
                color="primary "
                style={{ backgroundColor: "#00a82d", color: "white" }}
              >
                Get started
              </Buttons>
            </Link>
          </Flexcards>
          <Flexcards>
            <Listing>
              <li>
                <FlareIcon
                  size={18}
                  style={{ color: "#00a82d", marginRight: "5px" }}
                />
                Business
              </li>
              <li>=N={pageData.business}</li>
              <li>great notes</li>
              <li>Keep home and family together</li>
              <li>
                <CheckIcon size={15} /> Unlimited  to all features and customization
              </li>
              <li>
                <CheckIcon size={15} /> Manage tasks easily and  track their progress
              </li>
              <li>
                <CheckIcon size={15} /> Save 3x more notes
              </li>
              <li>
                <CheckIcon size={15} /> Find many items by a single search
              </li>
              <li>
                <CheckIcon size={15} /> Unlimited access tp meeting notes templates
              </li>
  
              <li>
                {" "}
                <CheckIcon size={15} /> Manage all projects and task in one place
              </li>
              <li>
                {" "}
                <CheckIcon size={15} /> Unlimited notes size{" "}
              </li>
            </Listing>

            <Link to="/login/signup">
              <Buttons
                variant="outlined"
                color="primary "
                style={{ backgroundColor: "#00a82d", color: "white" }}
              >
                Get started
              </Buttons>
            </Link>
          </Flexcards>
        </PlansSection>
      </Container>
      <Footers />
    </>
  );
};

export default Plans;
