import React from "react";
import styled from "styled-components";
import Button from "@material-ui/core/Button";
import { Link, useHistory } from "react-router-dom";
import Home_Plans from "./pages/home_plans";
import SwiperCore, {
  Navigation,
  Pagination,
  // Scrollbar,
  A11y,
  Autoplay,
  EffectFade,
} from "swiper";
import "swiper/components/effect-fade/effect-fade.scss";

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.scss";
const Container = styled.div`
  min-height: 300px;
  text-align: center;
  //   padding: 10px;
  padding-top: 190px;
  width: 100%;
`;

SwiperCore.use([Navigation, Pagination, A11y, Autoplay, EffectFade]);

const BigText = styled.h4`
  font-size: 55px;
  color: black;
  letter-spacing: 2px;
  font-weight: 600;
  padding: 0;
  margin: 0;
  @media (max-width: 1100px) {
    font-size: 40px;
  }
`;
const slideSWiper = {
  width: "100%",
  alignItems: "center",
  textAlign: "center",
  backgroundColor: "#f1f0f0",
  display: "flex",
  flexDirection: "row",
  flexWrap: "wrap",
  justifyContent: "center",
};
const MidTextMain = styled.p`
  font-size: 30px;
  // color: #696969;
  font-weight: 400;
  letter-spacing: 2px;
  @media (max-width: 1100px) {
    font-size: 24px;
  }
`;
const MidTextMain2 = styled.p`
  font-size: 30px;
  color: #696969;
  font-weight: 600;
  //   @media (max-width: 1100px) {
  //     display: none;
  //   }
`;

const SmallTextnav = styled.p`
  font-size: 20px;
  // color: #f2f2f2;
  text-decoration: underline;
  font-weight: 300;
  //   &:nth-child(n) {
  //     margin-right: 20px;
  //   }
  @media (max-width: 1100px) {
    font-size: 15px;
  }
`;
const SmallTextnav2 = styled.p`
  font-size: 20px;
  color: grey;

  font-weight: 500;
  //   &:nth-child(n) {
  //     margin-right: 20px;
  //   }
  @media (max-width: 1100px) {
    font-size: 15px;
  }
`;
const Ptitle = styled.p`
  // font-family: "Soleil_Bold", Helvetica, Arial, sans-serif;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 1.5;
  margin: 0;
  font-weight: 900;
`;
const Psmall = styled.p`
  font-size: 17px;
  letter-spacing: 1;

  // font-family: "Soleil_Bold", Helvetica, Arial, sans-serif;
  // font-family: 'Poppins', sans-serif;
`;
const H3 = styled.h3`
  font-family: "Soleil_Medium", Helvetica, Arial, sans-serif;
  font-size: 20px;
  line-height: 150%;
  font-weight: 300;
  letter-spacing: 3px;
`;
const H4 = styled.h3`
  font-family: "Soleil_Medium", Helvetica, Arial, sans-serif;
  font-size: 28px;
  line-height: 150%;
  font-weight: 600;
`;
const H2 = styled.h3`
  font-family: "Soleil_Medium", Helvetica, Arial, sans-serif;
  font-size: 44px;
  line-height: 150%;
`;
const SmallTextnav4 = styled.p`
  font-size: 17px;

  color: #686868;
  font-weight: 500;

  //   &:nth-child(n) {
  //     margin-right: 20px;
  //   }
  @media (max-width: 1100px) {
    font-size: 15px;
  }
`;

// const SmallTextnav = styled.p`
//   font-size: 18px;
//   color: grey;
//   font-weight: 500;
//   &:nth-child(n) {
//     margin-right: 20px;
//   }
//   @media (max-width: 1100px) {
//     display: none;
//   }
// `;

const LaptopSection = styled.div`
  background-color: color;
  min-height: 300px;
  width: 100%;
  display: flex;
  padding: 10px;
  box-sizing: border-box;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
`;
const Reviews = styled.div`
  background-color: #f1f0f0;
  min-height: 500px;
  width: 100%;
  display: flex;
  box-sizing: border-box;
  padding: 10px;
  //   flex-wrap: wrap;
  //   flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 10px;
`;
const Laptop = styled.img`
  width: 1000px;

  @media (max-width: 1100px) {
    max-width: 300px;
  }
`;

const Listing = styled.ul`
  &:nth-child(n) {
    // margin-right: 20px;
  }
  list-style: none;

  padding: 0px;
  li {
    margin-bottom: 5px;
  }
`;

const Div = styled.div`
  max-width: 300px;
  @media (max-width: 1100px) {
    max-width: 80%;
  }
`;
const Line = styled.div`
  width: 80px;
  height: 2px;
  background-color: #00a82d;
`;
const MainLanging = () => {
  const history = useHistory();
  return (
    <Container>
      <BigText>Archieve More With Ascriptinmyname</BigText>

      <MidTextMain>
        Save your best moments and also retrieve them fast{" "}
      </MidTextMain>
      <SmallTextnav>
        <Link to="/Login">Already have an account? sign in</Link>
      </SmallTextnav>
      <Button
        onClick={() => history.push("/plans")}
        variant="contained"
        color="primary"
        style={{ backgroundColor: "#00a82d", width: "200px", height: "50px" }}
      >
        Sign Up Now
      </Button>
      <LaptopSection>
        <Laptop src="./laptop.jpg" alt="laptop" />
        <Div>
          <Listing>
            <li>
              <Ptitle>WORK EVERYWHERE </Ptitle>
              <div
                style={{
                  textAlign: "center",
                  width: "100%",
                  justifyContent: "center",
                  display: "flex",
                  marginTop: "10px",
                }}
              >
                {" "}
                <Line />
              </div>
            </li>
            <li>
              <Psmall>
                Dont forget anhything and tackle any job with your notes, tasks,
                and everything in one place
              </Psmall>
            </li>
          </Listing>
          <Listing>
            <li>
              <Ptitle> TACKLE YOUR PROJECTS</Ptitle>
              <div
                style={{
                  textAlign: "center",
                  width: "100%",
                  justifyContent: "center",
                  display: "flex",
                  marginTop: "10px",
                }}
              >
                {" "}
                <Line />
              </div>
            </li>
            <li>
              <Psmall>
                Search and get what you need for your task or projects fast,
                when you need it with fast, accurate search features.
              </Psmall>
            </li>
          </Listing>
        </Div>
      </LaptopSection>
      <Reviews>
        <Swiper
          effect="fade"
          loop={true}
          autoplay={true}
          spaceBetween={70}
          slidesPerView={1}
          navigation={true}
          onSwiper={(swiper) => {}}
          onSlideChange={(e) => {
            // console.log(e.activeIndex);
          }}
          style={{ height: "90%" }}
        >
          <SwiperSlide style={slideSWiper}>
            <H3>
              {/* <SmallTextnav2 style={{ lineHeight: "2rem", maxWidth: "500px" }}> */}
              "I have had the product for 15 minutes and have been able to
              <br />
              use it, it has been a wonderfull experience. I was so excited
              <br /> to use it because it would cross over between my
              <br /> journaling and bible time.
              <br />
              <br />
             <b>--Dr jones</b>
              {/* </SmallTextnav2> */}
            </H3>
          </SwiperSlide>
          <SwiperSlide style={slideSWiper}>
            <H3>
              The app is very functional when it comes to saving
              <br /> notes and pictures. notes can not be used after they
              <br /> are saved.
              <br />
              <br />
              <br />
           
              <b> ---Dee</b>
            </H3>
          </SwiperSlide>
          <SwiperSlide style={slideSWiper}>
            <H3>
              "I love this app and NEED it in my life. It cleans up all those
              <br />
              notification icons that collect along the top of the <br />
              screen and saves notifications in case you need to go back
              <br /> ,keep it up.
              <br />
              <br />
            <b>  --Engr kelis</b>
            </H3>
          </SwiperSlide>
        </Swiper>
      </Reviews>
      <Home_Plans />
    </Container>
  );
};

export default MainLanging;
