import React from "react";
import styled from "styled-components";
import Button from "@material-ui/core/Button";
import { Link, useHistory } from "react-router-dom";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import ImageSearchIcon from "@material-ui/icons/ImageSearch";
import PermMediaIcon from "@material-ui/icons/PermMedia";
import RotateLeftIcon from "@material-ui/icons/RotateLeft";
import Footers from "./pages/footer";
import SwiperCore, {
  Navigation,
  Pagination,
  // Scrollbar,
  A11y,
  Autoplay,
  EffectFade,
} from "swiper";
import "swiper/components/effect-fade/effect-fade.scss";

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.scss";
import NavBar from "./Navbar";
const Container = styled.div`
  min-height: 300px;
  text-align: center;
  //   padding: 10px;
  padding-top: 130px;
  width: 100%;
`;

SwiperCore.use([Navigation, Pagination, A11y, Autoplay, EffectFade]);

const BigText = styled.p`
  font-size: 60px;
  color: black;
  font-weight: 600;
  @media (max-width: 1100px) {
    font-size: 40px;
  }
`;
const Pbig = styled.p`
  font-size: 25px;
  line-spacing: 2px;
  color: black;
  font-weight: 600;
  @media (max-width: 1100px) {
    font-size: 40px;
  }
`;
const slideSWiper = {
  width: "100%",
  alignItems: "center",
  textAlign: "center",
  backgroundColor: "#f1f0f0",
  display: "flex",
  flexDirection: "row",
  flexWrap: "wrap",
  justifyContent: "center",
};
const MidTextMain = styled.p`
  font-size: 20px;
  // color: #696969;
  font-weight: 400;

  @media (max-width: 1100px) {
    font-size: 24px;
  }
`;
const MidTextMain2 = styled.p`
  font-size: 30px;
  color: #696969;
  font-weight: 600;
  //   @media (max-width: 1100px) {
  //     display: none;
  //   }
`;

const SmallTextnav = styled.p`
  font-size: 20px;
  color: grey;
  text-decoration: underline;
  font-weight: 500;
  //   &:nth-child(n) {
  //     margin-right: 20px;
  //   }
  @media (max-width: 1100px) {
    font-size: 15px;
  }
`;
const SmallTextnav2 = styled.p`
  font-size: 20px;
  color: grey;

  font-weight: 500;
  //   &:nth-child(n) {
  //     margin-right: 20px;
  //   }
  @media (max-width: 1100px) {
    font-size: 15px;
  }
`;
const Ptitle = styled.p`
  // font-family: "Soleil_Bold", Helvetica, Arial, sans-serif;
  font-size: 25px;
  letter-spacing: 1px;
  line-height: 150%;
  font-weight: 800;
`;
const Psmall = styled.p`
  font-size: 16px;

  font-weight: 400;
  letter-spacing: 1px;

  // font-family: 'Poppins', sans-serif;
`;
const H3 = styled.h3`
  // font-family: "Soleil_Medium", Helvetica, Arial, sans-serif;
  font-size: 24px;
  letter-spacing: 1.5px;
  line-height: 150%;
  font-weight: 900;
`;
const H4 = styled.h4`
  // font-family: "Soleil_Medium", Helvetica, Arial, sans-serif;
  font-size: 20px;
  line-height: 150%;
  font-weight: 500;
  letter-spacing: 2px;
  font-family: "Poppins", sans-serif;
`;
const H2 = styled.h2`
  font-family: "Soleil_Medium", Helvetica, Arial, sans-serif;
  font-size: 44px;
  line-height: 150%;
`;
const SmallTextnav4 = styled.p`
  font-size: 17px;

  color: #686868;
  font-weight: 500;

  //   &:nth-child(n) {
  //     margin-right: 20px;
  //   }
  @media (max-width: 1100px) {
    font-size: 15px;
  }
`;

