//   }
// `;

const LaptopSection = styled.div`
  background-color: color;
  min-height: 300px;
  width: 100%;
  display: flex;
  padding: 10px;
  box-sizing: border-box;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
`;
const Reviews = styled.div`
  background-color: #f1f0f0;
  min-height: 500px;
  width: 100%;
  display: flex;
  box-sizing: border-box;
  padding: 10px;
  //   flex-wrap: wrap;
  //   flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 10px;
`;
const Laptop = styled.img`
  width: 1000px;

  @media (max-width: 1100px) {
    max-width: 300px;
  }
`;

const Listing = styled.ul`
  &:nth-child(n) {
    // margin-right: 20px;
  }
  list-style: none;

  padding: 0px;
  li {
    margin-bottom: 20px;
  }
`;

const Div = styled.div`
  max-width: 300px;
  @media (max-width: 1100px) {
    max-width: 80%;
  }
`;

const CardSection1 = styled.div`
  width: 100%;
  flex-wrap: wrap;
  min-height: 40px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 50px;
`;
const FeaturesCard = styled.div`
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  min-height: 350px;
  width: 280px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 10px;
`;
const Line = styled.div`
  width: 80px;
  height: 2px;
  background-color: #00a82d;
`;
const WhyNotes = () => {
  const history = useHistory();
  return (
    <>
      <NavBar />

      <Container>
        <BigText>Why To Choose Ascriptinmyname</BigText>
        <MidTextMain>
        Ascriptinmyname gives you all that you will need to keep  history of your personal or organizations<br/> activities
          secured, project planning and implementation, and easy ways to <br/> retrieve them fast when you need it
        </MidTextMain>

        <Button
          onClick={() => history.push("/plans")}
          variant="contained"
          color="primary"
          style={{ backgroundColor: "#00a82d", width: "200px", height: "50px" }}
        >
          <small> Sign up now to begin..</small>
        </Button>
        <CardSection1>
          <FeaturesCard>
            <NoteAddIcon
              fontSize="large"
              style={{
                color: "#00a82d",
                fontSize: "60px",
              }}
            />
            <H3>Save everything</H3>
            <Psmall>
            Save rich format text and documents including to-do lists.
            </Psmall>
          </FeaturesCard>
          <FeaturesCard>
            <ImageSearchIcon
              fontSize="large"
              style={{ color: "#00a82d", fontSize: "60px" }}
            />
            <H3>Keep all together</H3>
            <Psmall>
           Keep Records of your business to help you understand how you are doing .
            </Psmall>
          </FeaturesCard>
          <FeaturesCard>
            <RotateLeftIcon
              fontSize="large"
              style={{ color: "#00a82d", fontSize: "60px" }}
            />
            <H3>Retrieve it fast</H3>
            <Psmall>
            Good record keeping doesnt have to be hard; the key is to find a system that works for you
            </Psmall>
          </FeaturesCard>
          <FeaturesCard>
            <PermMediaIcon
              fontSize="large"
              style={{ color: "rgb(34, 139, 34)", fontSize: "60px" }}
            />
            <H3>Move Allong with it</H3>
            <Psmall>
            keep evidence of all transactions and retrieve fast in a matter of seconds
            </Psmall>
          </FeaturesCard>
        </CardSection1>
        <LaptopSection>
          <Laptop src="./laptop.jpg" alt="laptop" />
          <Div>
            <Div>
              <Listing>
                <li>
                  <Ptitle>WORK EVERYWHERE </Ptitle>
                  <div
                    style={{
                      textAlign: "center",
                      width: "100%",
                      justifyContent: "center",
                      display: "flex",
                      marginTop: "10px",
                    }}
                  >
                    {" "}
                    <Line />
                  </div>
                </li>
                <li>
                  <Psmall>
                    Dont forget anhything and tackle any job with your notes,
                    tasks, and everything in one place
                  </Psmall>
                </li>
              </Listing>
              <Listing>
                <li>
                  <Ptitle> TACKLE YOUR PROJECTS</Ptitle>
                  <div
                    style={{
                      textAlign: "center",
                      width: "100%",
                      justifyContent: "center",
                      display: "flex",
                      marginTop: "10px",
                    }}
                  >
                    {" "}
                    <Line />
                  </div>
                </li>
                <li>
                  <Psmall>
                    Search and get what you need for your task or projects fast,
                    when you need it with fast, accurate search features.
                  </Psmall>
                </li>
              </Listing>
            </Div>
          </Div>
        </LaptopSection>
        <Reviews>
          <Swiper
            effect="fade"
            loop={true}
            autoplay={true}
            spaceBetween={50}
            slidesPerView={1}
            navigation={true}
            onSwiper={(swiper) => {}}
            onSlideChange={(e) => {
              // console.log(e.activeIndex);
            }}
            style={{ height: "90%" }}
          >
           <SwiperSlide style={slideSWiper}>
            <H3>
              {/* <SmallTextnav2 style={{ lineHeight: "2rem", maxWidth: "500px" }}> */}
              "I have had the product for 15 minutes and have been able to
              <br />
              use it, it has been a wonderfull experience. I was so excited
              <br /> to use it because it would cross over between my
              <br /> journaling and bible time.
              <br />
              <br />
             <b>--Dr jones</b>
              {/* </SmallTextnav2> */}
            </H3>
          </SwiperSlide>
          <SwiperSlide style={slideSWiper}>
            <H3>
              The app is very functional when it comes to saving
              <br /> notes and pictures. notes can not be used after they
              <br /> are saved.
              <br />
              <br />
              <br />
            
              <b> ---Dee</b>
            </H3>
          </SwiperSlide>
          <SwiperSlide style={slideSWiper}>
            <H3>
              "I love this app and NEED it in my life. It cleans up all those
              <br />
              notification icons that collect along the top of the <br />
              screen and saves notifications in case you need to go back
              <br /> ,keep it up.
              <br />
              <br />
            <b>  --Engr kelis</b>
            </H3>
          </SwiperSlide>
          </Swiper>
        </Reviews>
        <Footers />
      </Container>
    </>
  );
};

export default WhyNotes;
