import React from "react";
import styled from "styled-components";
import NavBar from "../Navbar";
import PlansStepper from "../PlansStepper";
import Button from "@material-ui/core/Button";
import { Link ,useHistory} from "react-router-dom";
import { useSelector } from "react-redux";
import Moment from 'react-moment';
import CheckBox from "react-animated-checkbox"
import Footers from "./footer";
const Container = styled.div`
  background-color: #ffffff;
  //   background-color: #cfe8fc;
  flex-direction: column;
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
`;
const BigTextnav = styled.p`
  font-size: 20px;
  color: grey;
  font-weight: 300;
  @media (max-width: 1100px) {
    font-size: 12px;
  }
`;
const Line = styled.div`
  height: 1px;
  width: 100%;
  background-color: grey;
  margin-bottom: 30px;
`;
const Line2 = styled.div`
  height: 0.4px;
  width: 100%;

  background-color: #f1f0f0;
  margin-bottom: 30px;
`;

const Section = styled.div`
  width: 80%;
  flex-direction: column;
  display: flex;
  align-items: center;

  min-height: 80vh;
`;

const Marging = styled.div`
  height: 120px;
`;
const Marging2 = styled.div`
  height: 30px;
`;
const ListingContainer = styled.div`
  flex-direction: row;
  width: 100%;
  display: flex;

  justify-content: center;

  align-items: center;
  * {
    margin-right: 20px;
  }
`;
const Listing = styled.ul`
  max-width: 50%;
  height: 100px;
  padding: 0;
  li {
    font-size: 12px;
    color: grey;
    list-style-type: square;
    margin: 10px;
  }
`;
const AccountSection = styled.div`
  min-height: 300px;
  background-color: rgb(247, 252, 252);
  width: 100%;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
`;
const Buttons = styled(Button)`
  min-width: 100px;
  height: 30px;
  text-align: center;

  && {
    color: green;
  }
`;
const SuccessPay = () => {
  const history=useHistory()
  const[checked,setChecked] =React.useState(false)
  const CurrentUser = useSelector((state) => state.user.currentUser);
  const userdata = CurrentUser && CurrentUser.userdata;

const setCheck=()=>{
  setTimeout(() => {
    setChecked(true)
  }, 1000);
  setTimeout(()=>{
    history.push("/")
  },6000)
}

React.useEffect(()=>{
  setCheck()
},[])

  return (
    <>
      <NavBar />
      <Marging />
      <Container>
        <Section>
         <div>

         <CheckBox
  checked={checked}
  checkBoxStyle={{
    checkedColor: "#34b93d",
    size: 100,
    unCheckedColor: "#b8b8b8"
  }}
  duration={400}
  // onClick={()=>this.handleClick()}
/>

         </div>
         <Marging2 />
         <BigTextnav style={{ alignSelf: "center" }}>
        Youy Payment was successfull, Your account plan has been upgraded.
      
          </BigTextnav>
     
          {/* Account Summary */}
          <Line2 />
      
          <Marging />
        
          <Line />
        </Section>
      </Container>
      <div style={{height:"80px"}}/>
      <Footers />
    </>
  );
};

export default SuccessPay;
