import React from "react";
import styled from "styled-components";
import NavBar from "../Navbar";
import Button from "@material-ui/core/Button";
import Footers from "./footer";
import BusinessIcon from "@material-ui/icons/Business";
import CheckIcon from "@material-ui/icons/Check";
import StarIcon from "@material-ui/icons/Star";
import FlareIcon from "@material-ui/icons/Flare";
import { Link } from "react-router-dom";
import axios from "axios";

const Container = styled.div`
  min-height: 300px;

  padding: 10%;
  box-sizing: border-box;
  padding-top: 130px;
  width: 100%;
  @media (max-size: 680px) {
    padding: 5%;
  }
`;
const Buttons = styled(Button)`
  width: 200px;
  height: 40px;

  && {
    margin:3px;

    color: green;
    border-color: green;
    &:focus {
      background-color: blue;
    }
`;
const BigText = styled.p`
  font-size: 60px;
  color: black;
  font-weight: 600;
  @media (max-width: 1100px) {
    font-size: 30px;
  }
`;

const PlansSection = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px;
  box-sizing: border-box;
  margin-top:100px;
`;

const Flexcards = styled.div`
  width: 320px;
  border: 1px solid green;
  min-height: 400px;
  border-radius: 8px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 680px) {
    margin-left: 2px;
    margin-right: 3px;
    width: 90%;
  }
`;
const Listing = styled.ul`
  display: flex;
  margin: 0;
  list-style-type: none;
  padding: 0;

  flex-direction: column;
  align-items: flex-start;
  width: 60%;

  li {
    display: flex;
    font-size: 16px;
    color: black;
    padding: 10px;


    &:nth-child(1) {
      font-size: 35px;
      color: green;
      font-weight: 600;
      @media (max-width: 1100px) {
        font-size: 30px;
      }
    }
  }
`;
const MidText = styled.p``;
const Aboutus = () => {
  const [isLoading, setIsLoading] = React.useState("");
  const [homedata, setHomedata] = React.useState({ users: 0, notes: 0 });
  const [privacyData, setPrivacydata] = React.useState([])
  const [pageData, setPageData] = React.useState({ premium: 0, business: 0 });

  const gettPrivacy = (values) => {
    setIsLoading(true)
    // console.log(process.env.REACT_APP_API_URL)
    axios
     .get(`${process.env.REACT_APP_API_URL}/aboutus`)
      // .get(`http://localhost:8080/aboutus`)
      .then((res) => {
        setIsLoading(false)

        setPrivacydata(res.data.userData)
      })
      .catch((err) => {
        alert('error occured')
        setIsLoading(false)
        console.log(err)
      })
  }
  React.useEffect(() => {
  gettPrivacy()
  }, []);

  return (
    <>
      <NavBar />
      <Container>
        <BigText style={{textAlign:"center"}}>About notes </BigText>

        <PlansSection>
          {privacyData.map((item, index) => (
            <Listing>
              <li>{item.title}</li>
              <li>{item.body}</li>
            </Listing>
          ))}
        </PlansSection>
      
      </Container>
      <Footers />
    </>
  );
};

export default Aboutus;
